<script>
/* eslint-disable */
import { Pie } from 'vue-chartjs';
import { colorTheme } from "../../store/state";

export default {
  extends: Pie,
  props: ['labels'],
  async mounted () {
    this.renderChart({
      labels: Object.keys(this.labels),
      datasets: [
        {
          label: 'Amount',
          backgroundColor: Object.keys(this.labels).map(label => colorTheme[label]),
          data: Object.values(this.labels).map(stock => stock.amount),
        },
      ]
    }, {
      responsive: true,
      maintainAspectRatio: false
    });
  }
}
</script>
